@mixin set-button($value, $bg: #000, $color :#FFF, $per: 5%) {
	.btn {
		&.btn-#{$value} {
			@include border-radius(3px);
			@include link ($color);
			@include bg ($bg, darken($bg, $per), darken($bg, $per)); } } }

@include set-button(link, $color-link);
@include set-button(hover, $color-hover);
@include set-button(muted, $color-brown);
@include set-button(main, $color-main);
@include set-button(con, $color-con);
@include set-button(danger, $color-danger);
@include set-button(primary, $color-peter-river);
@include set-button(warning, $color-warning);
@include set-button(success, $color-success);
@include set-button(voted, $color-sun-flower);
@include set-button(amethyst, $color-amethyst);
@include set-button(turquoise, $color-turquoise);
