@mixin set-indicator($value, $bg: #000) {
	.ribbon {
		width: 115px;
		height: 115px;
		overflow: hidden;
		position: absolute;
		top: -3px;
		left: -3px;
		.ribbon-text {
			@include transform(rotate(-45deg));
			@include fz(0.6875rem);
			@include fw(700);
			letter-spacing: 0.24px;
			letter-spacing: 0.015rem;
			text-transform: uppercase;
			color: $color-white;
			text-align: center;
			@include position(2, 21px, -24px, '', '');
			width: 110px;
			height: 20px;
			@include pad(2px);
			background: $color-brown;
			&:before {
				border-top: 3px solid darken($color-brown, 5%);
				left: 0;
				content: '';
				border-left: 3px solid transparent;
				border-right: 3px solid transparent;
				position: absolute;
				bottom: -3px; }
			&:after {
				right: 0;
				content: '';
				border-left: 3px solid transparent;
				border-right: 3px solid transparent;
				position: absolute;
				bottom: -3px;
				border-top: 3px solid darken($color-brown, 5%); } }
		&.ribbon-#{$value} {
			.ribbon-text {
				background: #{$bg};
				&:before {
					border-top-color: #{$bg}; }
				&:after {
					border-top-color: #{$bg}; } } } } }

@include set-indicator(link, $color-link);
@include set-indicator(hover, $color-hover);
@include set-indicator(muted, $color-brown);
@include set-indicator(main, $color-main);
@include set-indicator(con, $color-con);
@include set-indicator(danger, $color-danger);
@include set-indicator(primary, $color-peter-river);
@include set-indicator(warning, $color-warning);
@include set-indicator(success, $color-success);
@include set-indicator(voted, $color-sun-flower);
@include set-indicator(amethyst, $color-amethyst);
@include set-indicator(turquoise, $color-turquoise);
