header {
	font-family: 'BN-Fonts';
	.navbar {
		@include border-radius(0);
		@include bg($color-white);
		@include box-shadow(0, 2px,4px, darken($color-white,30%), "");
		@include transition(all .5s ease-in-out);
		.mobile {
			.navbar-brand {
				max-height: 34px;
				@include pad(0);
				@include mar(0);
				.bn {
					// height: 2.2rem
					min-height: 34px;
					min-width: 62px;
					width: 62px; } }
			.navbar-toggler {
				@include bg($color-main, darken($color-main,5%), darken($color-main,5%));
				@include border-radius(0);
				@include link($color-white);
				@include border-radius(3px);
				@include pad(.35rem .5rem);
				@include mar(2px, top);
				i {}
				@include fz(1.46rem); }
			.globalmenu {
				height: 2.2rem;
				width: 2.2rem;
				@include lh(2.2rem);
				@include mar(0, top);
				@include bg($color-con, $color-main, $color-main);
				@include link($color-white, $color-con, $color-con);
				@include border($color-white, 1px, 1, '');
				@include pad(0);
				display: block;
				text-align: center;
				@include border-radius(3px);
				&:focus {
					outline: none; }
				i {
					text-align: center; } } }
		#BNGlobalMenu {
			@include media-breakpoint-down(sm) {
				@include mar(.5rem,top);
				@include border(darken($color-white,5%), 1px, 1, top); }
			.globalmenu {
				height: 2rem;
				width: 2rem;
				@include lh(2rem);
				@include mar(.5rem, top);
				@include bg($color-con, $color-main, $color-main);
				@include link($color-white, $color-con, $color-con);
				@include border($color-white, 1px, 1, '');
				@include pad(0);
				display: block;
				text-align: center;
				@include border-radius(3px);
				&:focus {
					outline: none; }
				i {
					text-align: center; } }
			.navbar-brand {
				max-height: 3rem;
				@include lh(3rem);
				@include pad(0);
				.bn {
					height: 3rem;
					width: 70px;
					&:hover {
						&:before {
							background-color: $color-con; } } } }
			.nav {
				.nav-item {
					float: none;
					+ .nav-item {
						@include mar(0); }
					@include media-breakpoint-down(sm) {
						+ .nav-item {
							@include mar(0); } }
					.nav-link {
						@include fz(1.1rem);
						text-transform: uppercase;
						@include fw(bold);
						@include pad(.75rem, right);
						@include pad(.75rem, left);
						color: darken($color-white,40%);
						.label {
							@include lh(1rem);
							@include fw(400);
							@include mar(.2rem, left); }
						&.textnote {
							@include link($color-tb-02-con);
							text-decoration: underline; }

						&.button {
							display: block;
							@include bg($color-con, darken($color-con, 10%), darken($color-con, 10%));
							@include link($color-white);
							@include fw(300);
							// text-transform: uppercase
							letter-spacing: 1px;
							@include pad(1rem, left);
							@include pad(1rem, right);
							@include border-radius(3px);
							@include mar(.5rem 0);
							&.premium {
								@include bg($color-tb-02-link, darken($color-tb-02-link, 10%), darken($color-tb-02-link, 10%)); } } }
					&.active, &:hover, &:visited, &:active {
						.nav-link {
							position: relative;
							@include link($color-main, $color-main, $color-main);
							@include media-breakpoint-up(md) {
								&:before {
									content: '';
									width: 100%;
									height: 3px;
									background-color: $color-main;
									@include position(1,-0.5rem,'','', 0); } } } }

					.search {
						position: relative;
						@include lh(4rem);
						input {
							@include border-radius(4rem);
							@include lh(1.6rem);
							@include mar(1rem, top);
							@include mar(.5rem, bottom);
							@include border(0);
							@include bg(darken($color-white, 7%));
							@include transition; }
						label {
							@include position(1,0.15rem,'','', 1rem);
							@include mar(0);
							line-height: 2rem;
							height: 2rem;
							@include link(darken($color-white, 20%), darken($color-white, 40%), darken($color-white, 40%));
							@include fz(1.5rem);
							i {
								&:before {
									@include lh(2rem); } } } } }
				&.navbar-right {
					text-align: center;
					.nav-item {
						float: none; }
					@include media-breakpoint-up(md) {
						text-align: left;
						float: right;
						.nav-item {
							float: left;
							.textnote {
								white-space: nowrap;
								max-width: 200px;
								overflow: hidden;
								text-overflow: ellipsis; } } }
					// @include media-breakpoint-up(lg)
					// 	text-align: left
					// 	float: right
					// 	.nav-item
					// 		float: left
					// 		.textnote
					// 			white-space: nowrap
					// 			max-width: 200px
					// 			overflow: hidden
					// 			text-overflow: ellipsis
					@include media-breakpoint-up(xl) {
						.nav-item {
							.textnote {
								max-width: initial; } } } }

				@include media-breakpoint-up(md) {
					@include lh(2.2rem);
					.nav-item {
						float: left;
						.nav-link {
							&.button {
								@include lh(1.6rem);
								display: inline-block;
								@include fz(1rem);
								@include mar(.2rem 0); } }
						.search {
							input {
								width: 200px;
								@include mar(.3rem, top);
								@include mar(.3rem, bottom);
								&:focus {
									width: 250px; } } } } } } } } }


.menu-bn-floaticon,
.menu-bn-inlineicon,
.menu-bn-inline {
	header {
		.navbar {
			#BNGlobalMenu {
				.navbar-brand {
					display: none; }
				.nav {
					.nav-item {
						&.togglemenu {
							display: none; }
						&.togglesearch {
							@include mar(0, left); } } } } } } }



