@mixin set-label($value, $bg: #000, $color :#FFF) {
	.label {
		&.label-#{$value} {
			color: #{$color};
			padding: .18rem .35rem;
			white-space: nowrap;
			@include border-radius(3px);
			text-align: center;
			// font-size: .60rem
			background-color: #{$bg}; } } }

@include set-label(link, $color-link);
@include set-label(hover, $color-hover);
@include set-label(muted, $color-brown);
@include set-label(main, $color-main);
@include set-label(con, $color-con);
@include set-label(danger, $color-danger);
@include set-label(primary, $color-peter-river);
@include set-label(warning, $color-warning);
@include set-label(success, $color-success);
@include set-label(voted, $color-sun-flower);
@include set-label(amethyst, $color-amethyst);
@include set-label(turquoise, $color-turquoise);
