@mixin set-col($value) {
	ion-col {
		&[width-#{$value}] {
			-webkit-box-flex: 0;
			-webkit-flex: 0 0 #{$value};
			-moz-box-flex: 0;
			-moz-flex: 0 0 #{$value};
			-ms-flex: 0 0 #{$value};
			flex: 0 0 #{$value};
			max-width: #{$value}; } } }

@include set-col(5);
@include set-col(15);
@include set-col(30);
@include set-col(35);
@include set-col(40);
@include set-col(45);
@include set-col(55);
@include set-col(60);
@include set-col(65);
@include set-col(70);
@include set-col(85);
@include set-col(95);

@mixin set-offset($value) {
	ion-col {
		&[offset-#{$value}] {
			-webkit-box-flex: 0;
			-webkit-flex: 0 0 #{$value};
			-moz-box-flex: 0;
			-moz-flex: 0 0 #{$value};
			-ms-flex: 0 0 #{$value};
			flex: 0 0 #{$value};
			max-width: #{$value}; } } }

@include set-offset(5);
@include set-offset(15);
@include set-offset(30);
@include set-offset(35);
@include set-offset(40);
@include set-offset(45);
@include set-offset(55);
@include set-offset(60);
@include set-offset(65);
@include set-offset(70);
@include set-offset(85);
@include set-offset(95);
