.info-page {
	header {
		position: fixed;
		width: 100%;
		z-index: 100;
		@include media-breakpoint-up(md) {
			&.active {
				.navbar {
					background: rgba(255,255,255,.2);
					@include box-shadow(0, 1px, 0px, rgba(255,255,255,.3));
					#BNGlobalMenu {
						.navbar-brand {
							i {
								svg {
									* {
										fill: $color-white; } }
								&:before {
									background-color: $color-white; } } }
						.nav.navbar-right {
							.nav-item {
								.nav-link {
									color: rgba(255,255,255,.5); }
								&.active, &:hover {
									.nav-link {
										color: $color-white;
										&:before {
											background-color: $color-white;
											bottom: -0.65rem;
											top: initial; } } } } } } } } } }
	main {
		@include media-breakpoint-up(md) {
			@include pad(0,top); } }
	// SLIDERS
	.home-sliders {
		position: relative;
		overflow: hidden;
		.owl-carousel {
			&:after {
				@include position(1,0,0,'','');
				content: '';
				width: 100%;
				height: 100%;
				background: rgba(0,0,0,.3); } }
		.item {
			min-height: 400px;
			overflow: hidden;
			@include media-breakpoint-up(md) {
				min-height: 550px; } }
		.boxcontent {
			@include position(1,0,0,'','');
			z-index: 10;
			height: 100%;
			width: 100%;
			display: table;
			.boxitem {
				display: table-cell;
				vertical-align: bottom;
				width: 100%;
				height: 400px;
				color: $color-white;
				text-align: center;
				@include pad(0);
				aside {
					background: rgba(0,0,0,.5);
					@include pad(1rem);
					h2 {
						@include mar(0); } }
				h1 {
					@include text-shadow;
					font-family: 'BN-Fonts'; }
				h2 {
					@include text-shadow(1px, 1px, 1px, rgba(0, 0, 0, 0.4));
					@include fz(1.3rem);
					@include fw(200);
 }					// font-family: 'Raleway'
				@include media-breakpoint-up(md) {
					height: 550px;
					h1 {
						@include fz(4rem); }
					h2 {
						@include fz(1.5rem); } }
				.boxsearch {
					background: rgba(255,255,255,.8);
					display: inline-block;
					@include pad(10px);
					@include mar(15px,top);
					text-align: left;
					max-width: 300px;
					@include border(darken($color-white,60%), 1px, 1, '');
					h3 {
						@include fz(.9rem);
						color: $color-black;
						@include fw(600); }
					.input-group {
						* {
							@include border-radius(0); }
						button {
							text-transform: uppercase; } }
					@include media-breakpoint-up(md) {
						max-width: 380px; } } } } }
	// MAPS
	.home-maps {
		background-color: darken($color-white, 0%);
		overflow: hidden;
		@include border($color-main, 3px, 1, top);
		@include border($color-main, 3px, 1, bottom);
		position: relative;
		@include pad(2rem, bottom);
		@include media-breakpoint-up(lg) {
			@include pad(4rem,top); }
		.status {
			text-align: center;
			width: 100%;
			@include fz(.8rem);
			@include fw(bold);
			@include media-breakpoint-up(md) {
				// +position(1,'', 0, 2rem, '')
				@include position(1,'', 0, 1rem, '');
				@include fz(1rem); } }
		.status {
			@include position(1,'', 0, 2rem, '');
			text-align: center;
			width: 100%;
			@include fz(.8rem);
			@include media-breakpoint-up(md) {
				@include position(1,'', 0, 1rem, '');
				@include fw(bold);
				@include fz(1rem); } }
		.statustop {
			font-family: 'BN-Fonts';
			text-align: center;
			width: 100%;
			max-width: 600px;
			@include pad(0 2rem);
			p {
				@include fz(.8rem); }
			h2 {
				color: $color-main;
				@include fz(1.2rem); }
			@include media-breakpoint-up(md) {
				@include position(1,2rem, 50%, '', '');
				@include transform(translateX(-50%));
				p {
					@include fz(1rem); } }
			@include media-breakpoint-up(lg) {
				@include position(1,3rem, 50%, '', '');
				h2 {
					@include fz(2rem); } } }
		figure {
			position: relative;
			max-width: 1140px;
			@include mar(0 auto);
			img {
				width: 100%; }
			.areas {
				.pulsating-circle {
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translateX(-50%) translateY(-50%);
					width: 10px;
					height: 10px;
					&:after {
						content: '';
						position: absolute;
						left: 0;
						top: 0;
						display: block;
						width: 100%;
						height: 100%;
						background-color: $color-main;
						border: 2px solid $color-white;
						border-radius: 15px;
						@include box-shadow(0, 0, 5px, rgba(red($color-main),green($color-main),blue($color-main),.3));
						@include animation(pulse-dot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -.4s infinite); }
					&:before {
						content: '';
						position: relative;
						display: block;
						width: 300%;
						height: 300%;
						box-sizing: border-box;
						margin-left: -100%;
						margin-top: -100%;
						border-radius: 45px;
						background-color: $color-main;
						@include animation(pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite); }
					span {
						height: 200px;
						width: 200px;
						position: absolute;
						left: 50%;
						top: 50%;
						margin-left: -100px;
						margin-top: -100px;
						opacity: 0;
						border: 1px solid darken($color-white, 15%);
						background: transparent;
						@include border-radius(50%);
						@include animation(flashmap 2s ease-out infinite);
						&:before {
							content: '';
							height: 100px;
							width: 100px;
							position: absolute;
							left: 50%;
							top: 50%;
							margin-left: -50px;
							margin-top: -50px;
							opacity: 0;
							border: 4px solid darken($color-white, 15%);
							background: transparent;
							@include border-radius(50%);
							@include animation(flashmap 2s ease-out infinite); }
						&:after {
							content: '';
							height: 160px;
							width: 160px;
							position: absolute;
							left: 50%;
							top: 50%;
							margin-left: -80px;
							margin-top: -80px;
							opacity: 0;
							border: 2px solid darken($color-white, 15%);
							background: transparent;
							@include border-radius(50%);
							@include animation(flashmap 2s ease-out infinite); } }
					i {
						position: absolute;
						left: 50%;
						top: 50%;
						margin-left: -100px;
						margin-top: -100px;
						width: 200px;
						height: 200px;
						&:after {
							content: '';
							display: block;
							position: absolute;
							left: 50%;
							top: 50%;
							margin-left: -50px;
							margin-top: -50px;
							width: 100px;
							height: 100px;
							background-color: $color-main;
							@include border-radius(50%);
							filter: blur(30px);
							-webkit-filter: blur(30px);
							@include animation(lightflashh .5s ease-out infinite); }
						&:before {
							content: '';
							position: absolute;
							left: 50%;
							top: 50%;
							margin-left: -100px;
							margin-top: -100px;
							display: block;
							width: 200px;
							height: 200px;
							background-color: $color-main;
							@include border-radius(50%);
							filter: blur(30px);
							-webkit-filter: blur(30px);
							@include animation(lightflash .7s ease-out infinite); } } }
				.item {
					position: absolute;
					width: 30px;
					height: 30px;
					display: none;
					&.mumbai {
						left: 61%;
						top: 43%;
						@include media-breakpoint-up(md) {
							left: 64%;
							top: 49%; }
						@include media-breakpoint-up(lg) {
							left: 65%;
							top: 51%; } }
					&.hongkong {
						left: 71%;
						top: 41%;
						@include media-breakpoint-up(md) {
							left: 74%;
							top: 47%; }
						@include media-breakpoint-up(lg) {
							left: 75%;
							top: 49%; } }
					&.japan {
						left: 77%;
						top: 35%;
						@include media-breakpoint-up(md) {
							left: 80%;
							top: 41%; }
						@include media-breakpoint-up(lg) {
							left: 81%;
							top: 43%; } }
					&.usa {
						left: 17%;
						top: 35%;
						@include media-breakpoint-up(md) {
							left: 23%;
							top: 41%; }
						@include media-breakpoint-up(lg) {
							left: 24%;
							top: 43%; } }
					&.thailand {
						left: 68%;
						top: 45%;
						@include media-breakpoint-up(md) {
							left: 71%;
							top: 51%; }
						@include media-breakpoint-up(lg) {
							left: 72%;
							top: 53%; } }
					&.singapore {
						left: 68%;
						top: 51%;
						@include media-breakpoint-up(md) {
							left: 71%;
							top: 57%; }
						@include media-breakpoint-up(lg) {
							left: 72%;
							top: 59%; } }
					.summary {
						@include position(1,5px, '', '', 30px);
						text-align: right;
						.name {
							background-color: $color-main;
							@include box-shadowset(inset 0 0px 0px 1px rgba(red($color-white),green($color-white),blue($color-white),.3));
							@include border($color-main, 1px ,1, '');
							@include border-radius(20px);
							// +lh(1.5rem)
							@include fz(.9rem);
							white-space: nowrap;
							color: $color-white;
							@include pad(0 .75rem);
							display: inline-block;
 }							// height: 1.5rem
						.sum {
							background-color: $color-con;
							@include box-shadowset(inset 0 0px 0px 1px rgba(red($color-white),green($color-white),blue($color-white),.3));
							@include border($color-con, 1px ,1, '');
							@include border-radius(20px);
							// +lh(1.2rem)
							@include fz(.8rem);
							white-space: nowrap;
							color: $color-white;
							@include pad(0 .5rem);
							// +mar(.15rem,top)
							display: inline-block; } } } } } }
	// HIW
	.home-howitworks {
		background-color: $color-white;
		background-image: url('../img/howitworks-bg.png');
		background-position: center center;
		position: relative;
		min-height: 200px;
		@include pad(5rem,top);
		@include animation(50s linear 0s normal none infinite animatebg);
		> .title {
			background-color: $color-main;
			font-family: 'BN-Fonts';
			@include pad(0 1rem);
			text-align: center;
			@include position(1,-1.5rem,50%,'','');
			margin-left: -25%;
			width: 50%;
			@include box-shadowset(inset 0 0px 0px 1px rgba(red($color-white),green($color-white),blue($color-white),1));
			@include border($color-main, 1px ,1, '');
			h2 {
				color: $color-white;
				@include mar(0);
				@include fz(1.1rem);
				@include lh(2.6rem);
				text-transform: uppercase; } }
		@include media-breakpoint-up(md) {
			@include pad(10rem,top);
			.title {
				@include position(1,3rem,-3px,'','');
				width: 300px;
				@include mar(0,left);
				h2 {
					@include fz(1.8rem);
					@include lh(3.3rem);
					height: 3rem;
					text-align: right; } } }
		// Tabs
		.nav {
			text-align: center;
			display: table;
			margin: 0 auto;
			font-family: 'BN-Fonts';
			.nav-item {
				+ .nav-item {
					@include mar(0); }
				.nav-link {
					@include border-radius(0);
					background-color: $color-white;
					color: $color-main;
					border: 1px solid $color-main;
					@include lh(1.5rem);
					text-transform: uppercase;
					i {
						font-size: 1.5rem; }
					&:hover {
						background-color: darken($color-white, 3%);
						color: $color-main; }
					&.active {
						background-color: $color-main;
						color: $color-white; } } } }
		.tab-content {
			// +mar(2rem 0)
			@include pad(2rem 1rem);
			overflow: hidden;
			@include media-breakpoint-up(md) {
				@include pad(3rem 3rem); } }

		// Box
		.boxhiw {
			position: relative;
			.owl-carousel {
				.owl-nav {
					@include media-breakpoint-up(lg) {
						margin-top: -40px; }
					> div {
						@include border-radius(0);
						@include pad(8px 12px);
						i {
							line-height: 1.8rem;
							height: 0;
							font-size: 1.4rem; } }
					.owl-prev {
						left: 0; }
					.owl-next {
						right: 0; } }
				.owl-dots {
					.owl-dot {
						span {
							@include border-radius(0); }
						&.active, &:hover {
							span {
								background-color: $color-main;
								@include box-shadowset(inset 0 0px 0px 1px rgba(red($color-white),green($color-white),blue($color-white),1));
								@include border($color-main, 1px ,1, ''); } } } } }
			.boxleft {
				.infotop {
					h4 {
						color: $color-main;
						margin-top: 1.5rem;
						text-transform: uppercase; } }
				.info {
					h4 {
						color: $color-main; }
					p {
						@include fz(.9rem);
						strong {
							color: $color-main; } } }
				p {
					strong {
						font-weight: 700;
						display: none; } }
				.item {
					padding: 10px 30px;
					overflow: hidden; } }
			.boxright {

				.titlec {
					font-family: 'BN-Fonts';
					font-size: 1.5rem;
					margin-bottom: 1rem;
					@include text-shadow(1px, 1px, 3px, rgba(255,255,255,.5));
					a {
						text-decoration: none;
						@include fw(bold);
						display: block;
						color: lighten($color-black, 20%);
						text-transform: uppercase;
						&:hover, &.active {
							color: $color-main; } } }
				p {
					font-size: .9rem;
					color: lighten($color-black, 30%);
 }					// display: none
				.content {
					padding: 0;
					p.goowl {
						color: darken($color-white,40%);
						&.active {
							color: $color-black;
							strong {
								color: $color-main; } } } } }

			@include media-breakpoint-up(lg) {
				.boxleft {
					width: 60%;
					float: left;
					height: 100%; }

				.boxright {
					width: 40%;
					float: right;
					height: 100%;
					padding: 0;
					.content {
						padding: 0 0 0 2.5rem; } } } } }
	// Testimonials
	.home-testimonials {
		font-family: 'BN-Fonts';
		min-height: 400px;
		background-color: $color-main;
		@include pad(3rem 1rem);
		text-align: center;
		@include media-breakpoint-up(md) {
			min-height: 350px;
			@include pad(4rem 2rem);
			h2 {
				@include fz(2.5rem);
				letter-spacing: 1px; } }
		h2 {
			color: $color-white; }
		.desc {
			margin: 2rem auto;
			h3 {
				@include fz(1.3rem);
				color: $color-white; }
			p {
				color: lighten($color-main, 20%);
				@include lh(1.3rem); }
			@include media-breakpoint-up(md) {
				max-width: 70%; }
			.testimonials {
				@include mar(4rem,top);
				@include pad(0 2rem);
				.owl-carousel {
					.owl-nav {
						@include media-breakpoint-up(md) {
							position: relative; }
						> div {
							@include border-radius(0);
							@include pad(8px 12px);
							background-color: lighten($color-main, 20%);
							&:hover, &.active {
								background-color: $color-white;
								color: $color-main; }
							i {
								line-height: 1.8rem;
								height: 0;
								font-size: 1.4rem; } }
						.owl-prev {
							left: 0; }
						.owl-next {
							right: 0; } }
					.owl-dots {
						.owl-dot {
							span {
								@include border-radius(0);
								background-color: lighten($color-main, 20%); }
							&.active, &:hover {
								span {
									background-color: $color-white; } } } }
					.item {
						.quote {
							position: relative;
							background-color: $color-white;
							color: $color-main;
							max-width: 550px;
							@include mar(0 auto);
							@include pad(1rem);
							@include media-breakpoint-up(md) {
								@include pad(2rem 3rem); }
							&:after {
								background-color: $color-white;
								content: '';
								width: 30px;
								height: 30px;
								@include position(1,'', 50%, -12px, '');
								@include transform(rotate(45deg));
								@include mar(-15px,left); } }
						.info {
							@include mar(3rem,top);
							figure {
								overflow: hidden;
								display: inline-block;
								img {
									border: 2px solid $color-white;
									width: 100px;
									height: 100px;
									@include border-radius(50%); }
								figcaption {
									.name {
										color: $color-white;
										@include fz(1.3rem);
										@include mar(1rem,top); }
									.jobs {
										color: lighten($color-main, 20%); } } } } } } } } } }




