.scrollbar-dynamic {
  width: 100%;
  max-height: 100%;
  height: 100%;
  position: relative;
  &.ps-container {
    > .ps-scrollbar-y-rail {
      z-index: 2000;
      @include mar(.5rem,top);
      @include mar(.5rem,bottom);
      > .ps-scrollbar-y {
        z-index: 2100;
        background-color: darken($color-white, 100%); }
      &:hover {
        width: 16px;
        @include border-radius(16px);
        > .ps-scrollbar-y {
          @include transition(all .1s ease-in-out);
          width: 12px;
          @include border-radius(12px);
          right: 2px;
 } } }          // margin: 2px 0
    &.ps-in-scrolling {
      > .ps-scrollbar-y-rail {
        width: 16px;
        @include border-radius(16px);
        > .ps-scrollbar-y {
          background-color: darken($color-white, 80%);
          @include transition(all .1s ease-in-out);
          width: 12px;
          @include border-radius(12px);
          right: 2px;
 } } } } }          // margin: 2px 0
