@import url(//fonts.googleapis.com/css?family=Ubuntu:400,500,700);

html, body {
	font-family: 'BN-Fonts';
	background-color: #292B37;
	font-size: 14px;
	width: 100%;
	height: 100%;
	@include selection {
		background-color: $color-main;
		color: $color-white; } }

.mdi {
	font-size: 1.14rem;
	line-height: 1.9rem; }
.bnapp {
	&.font-bn-roboto {
		font-family: 'Roboto'; }
	&.font-bn-lato {
		font-family: 'Lato'; }
	&.font-bn-opensans {
		font-family: 'Open Sans'; }
	&.font-bn-condensed {
		font-family: 'Roboto Condensed'; }
	&.font-bn-raleway {
		font-family: 'Raleway'; } }

.set-relative {
	position: relative; }

a {
	@include link($color-con, $color-main, $color-main); }

