  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 5px solid #795d9c;
  border-top-color: #d9d9d9;
  animation: counter-spin .5s infinite linear;
}

