  color: #87ceeb;
}

/* comment - skyblue */
pre .typ {
  color: #98fb98;
}

/* type    - lightgreen */
pre .lit {
  color: #cd5c5c;
}

/* literal - darkred */
pre .pun {
  color: #fff;
}

/* punctuation */
pre .pln {
  color: #fff;
}

/* plaintext */
pre .tag {
  color: #f0e68c;
  font-weight: bold;
}

/* html/xml tag    - lightyellow */
pre .atn {
  color: #bdb76b;
  font-weight: bold;
}

/* attribute name  - khaki */
pre .atv {
  color: #ffa0a0;
}

/* attribute value - pink */
pre .dec {
  color: #98fb98;
}

/* decimal         - lightgreen */
/* Specify class=linenums on a pre to get line numbering */
.sublime ol.linenums {
  margin-top: 0;
  margin-bottom: 0;
