.portfolio-page {
	width: 100%;
	height: 100%;
	overflow: hidden;
	background: $color-white url('../img/howitworks-bg.png');
	@include animation(10s linear 0s normal none infinite animatebg);
	text-align: center;
	.toggle-nav {
		color: $color-con1;
		.bar {
			background: $color-con1 !important; } }
	#canv {
		width: 100%;
		height: 100%;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
		@include opacity(.5); }
	main {
		@include pad(3rem 1rem);
		width: 100%;
		height: 100%;
		// overflow: hidden
		h2 {
			position: relative;
			&:before {
				width: 200px;
				height: 1px;
				display: block;
				position: absolute;
				top: 100%;
				left: 50%;
				content: '';
				margin-left: -100px;
				margin-top: 11px;
				background-color: #999999; }
			&:after {
				width: 60px;
				height: 3px;
				display: block;
				position: absolute;
				top: 100%;
				left: 50%;
				content: '';
				margin-left: -30px;
				margin-top: 10px;
				background-color: #333333; } }
		img {
			max-width: 90%;
			@include mar(2rem 0); } }
	.skillbar {
		position: relative;
		display: block;
		width: 100%;
		max-width: 650px;
		background: darken($color-white,20%);
		height: 26px;
		margin: 0 auto 15px auto;
		-webkit-transition: 0.4s linear;
		-moz-transition: 0.4s linear;
		-ms-transition: 0.4s linear;
		-o-transition: 0.4s linear;
		transition: 0.4s linear;
		-webkit-transition-property: width, background-color;
		-moz-transition-property: width, background-color;
		-ms-transition-property: width, background-color;
		-o-transition-property: width, background-color;
		transition-property: width, background-color; }


	.skillbar-title {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		max-width: 100px;
		font-weight: bold;
		font-size: 13px;
		color: #ffffff;
		background: #000; }


	.skillbar-title span {
		display: block;
		background: rgba(0, 0, 0, 0.1);
		padding: 0 20px;
		height: 26px;
		line-height: 26px; }


	.skillbar-bar {
		height: 26px;
		width: 0px;
		background: #6adcfa; }


	.skill-bar-percent {
		position: absolute;
		right: 10px;
		top: 0;
		font-size: 11px;
		height: 26px;
		line-height: 26px;
		color: #000; }
	.bn-timeline {
		background: #000;
		@include border-radius(3px);
		margin: 0 auto;
		display: inline-block;
		@include pad(.25rem .5rem);
		color: $color-white;
		@include mar(3rem,top); }
	.timeline-both-side {
		float: left;
		width: 100%;
		position: relative;
		box-sizing: border-box;
		@include mar(0 auto 3rem auto);
		@include pad(30px 0);
		&:before {
			background-color: #ccc;
			bottom: 0;
			content: " ";
			left: 50%;
			position: absolute;
			top: 0;
			width: 1px; }
		&:after {
			border-radius: 50%;
			bottom: -18px;
			content: "";
			height: 18px;
			left: 50%;
			margin-left: -9px;
			position: absolute;
			width: 18px;
			border: 2px solid #ccc;
			background: #fff; }
		li {
			position: relative;
			float: left;
			width: 100%;
			list-style-type: none;
			text-align: right;
			&.opposite-side {
				text-align: left; }
			.border-line {
				background-color: #ccc;
				font-size: 1.4em;
				height: 1px;
				left: 50%;
				margin-left: -8%;
				position: absolute;
				text-align: center;
				top: 50%;
				width: 8%;
				z-index: 100; }
			&.opposite-side .border-line {
				left: auto;
				right: 50%;
				margin-left: 0;
				margin-right: -8%; }
			.border-line:before {
				background-color: #ccc;
				content: "";
				height: 7px;
				position: absolute;
				right: -4px;
				top: -3px;
				width: 7px; }
			&.opposite-side .border-line:before {
				left: -4px;
				right: auto; }
			.timeline-description {
				border-radius: 2px;
				background-color: #f1f1f1;
				border: 1px solid #ccc;
				float: left;
				font-size: 13px;
				padding: 10px;
				position: relative;
				width: 42%; }
			&.opposite-side .timeline-description {
				float: right; } } } }
