.loading {
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,1);
	z-index: 9000;
	top: 0 {
  left: 0; }
	.loaderstyle {
		position: absolute;
		top: 50%;
		left: 50%;
		margin: -25px 0 0 -25px;
		height: 50px;
		width: 50px;
		border-radius: 50%;
		border: 5px solid $color-con1;
		border-top-color: darken($color-white, 15%);
		animation: spin3 1s infinite linear;
		&:before {
			content: '';
			position: absolute;
			top: 5px;
			left: 5px;
			height: 30px;
			width: 30px;
			border-radius: 50%;
			border: 5px solid $color-con1;
			border-top-color: darken($color-white, 15%);
			animation: counter-spin .5s infinite linear; }

		&:after {
			content: '';
			position: absolute;
			top: 15px;
			left: 15px;
			height: 10px;
			width: 10px;
			border-radius: 50%;
			border: 5px solid $color-con1;
			border-top-color: darken($color-white, 15%);
			animation: spin3 1s infinite linear; } } }



@include keyframes(spin3) {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(360deg); } }


@include keyframes(counter-spin) {
	0% {
		transform: rotate(360deg); }
	100% {
		transform: rotate(0deg); } }


.pace-running {
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		@include position(3, 0, 0, '', '');
		background: $color-con1;
		z-index: 2000;
		overflow: hidden; } }
