footer {
	background-color: darken($color-white ,80%);
	position: relative;
	z-index: 1;
	color: darken($color-white ,50%);
	overflow: hidden;
	@include pad(0);
	.container-fluid {
		@include media-breakpoint-up(md) {
			@include pad(0);
			.topfoot {
				display: table;
				width: 100%;
				@include mar(0); } } }
	.info {
		background-color: darken($color-white ,85%);
		@include pad(4rem 2rem);
		@include media-breakpoint-up(sm) {
			@include pad(4rem 4em); }
		@include media-breakpoint-up(md) {
			float: none;
			display: table-cell;
			vertical-align: middle; }
		p {
			@include mar(0,bottom);
			@include fz(.9rem);
			@include lh(1.3rem);
			a {
				display: block; } }
		.app-icon {
			&.bn {
				&:before {
					background-color: $color-white; }
				@include media-breakpoint-up(sm) {
					min-height: 60px;
					min-width: 100px;
					width: 100px; } } }
		.slogan {
			color: lighten($color-con1, 5%);
 } }			// color: $color-white
	.linkfooter {
		@include pad(4rem 2rem);
		background-color: darken($color-white ,80%);
		@include media-breakpoint-up(sm) {
			@include pad(4rem 4rem); }
		@include media-breakpoint-up(md) {
			float: none;
			display: table-cell;
			vertical-align: top; }
		h4 {
			color: $color-white;
			text-transform: uppercase;
			@include fw(300);
			letter-spacing: 1px;
			@include mar(2rem,bottom); }
		.list-group {
			.list-group-item {
				@include pad(0,left);
				@include pad(0,right);
				@include bg(none, none, none);
				@include border(0);
				@include fw(300);
				@include link(darken($color-white,50%),$color-white,$color-white);
				&:hover, &.active {
					text-decoration: underline; }
				@include media-breakpoint-up(md) {
					@include pad(.5rem,top);
					@include pad(.5rem,bottom); } } }
		.socials {
			@include media-breakpoint-down(md) {
				@include mar(2rem,top); }
			.list-inline {
				@include mar(0);
				.list-inline-item {
					@include fz(.9rem);
					@include link(darken($color-white, 85%), $color-white, $color-white);
					@include mar(1rem, right);
					a {
						display: block;
						height: 2rem;
						width: 2rem;
						@include lh(2rem);
						@include bg(darken($color-white,40%), $color-white, $color-white);
						@include link(darken($color-white,85%));
						@include border-radius(50%);
						text-align: center; }
					&:last-child {
						@include mar(0); } } }
			.app-icon {
				svg {
					* {
						stroke: $color-white; } } } } }
	.copyright {
		background-color: darken($color-white ,85%);
		@include pad(2rem);
		a {
			@include link($color-white, $color-con, $color-con); } } }
