@mixin set-text($value, $color :#000) {
	.text-#{$value} {
		color: #{$color}; } }
@mixin set-bg($value, $color :#000) {
	.bg-#{$value} {
		background-color: #{$color}; } }
@mixin set-size($value, $size) {
	.text-#{$value} {
		font-size: #{$size}; } }

@include set-text(main, $color-main);
@include set-text(con, $color-con);
@include set-text(con1, $color-con1);
@include set-text(con2, $color-con2);
@include set-text(con3, $color-con3);
@include set-text(con4, $color-con4);
@include set-text(danger, $color-danger);
@include set-text(primary, $color-peter-river);
@include set-text(warning, $color-warning);
@include set-text(success, $color-success);
@include set-text(voted, $color-sun-flower);
@include set-text(amethyst, $color-amethyst);
@include set-text(turquoise, $color-turquoise);
@include set-text(link, $color-link);
@include set-text(hover, $color-hover);

@include set-size(mini, .65rem);
@include set-size(xs, .75rem);
@include set-size(sm, .85rem);
@include set-size(md, 1rem);
@include set-size(lg, 1.3rem);
@include set-size(xl, 2rem);

@include set-bg(muted, $color-brown);
@include set-bg(main, $color-main);
@include set-bg(con, $color-con);
@include set-bg(danger, $color-danger);
@include set-bg(primary, $color-peter-river);
@include set-bg(warning, $color-warning);
@include set-bg(success, $color-success);
@include set-bg(voted, $color-sun-flower);
@include set-bg(amethyst, $color-amethyst);
@include set-bg(turquoise, $color-turquoise);
@include set-bg(link, $color-link);
@include set-bg(hover, $color-hover);
