// Margin
$gennumtop: 0;
$gennumbot: 0;
$gennumtoprem: 0;
$gennumbotrem: 0;
$lineheight: 0;
@while $gennumtop < 200 {
  .mrt-#{$gennumtop} {
    margin-top: #{$gennumtop}px; }
  .mrt-xs-#{$gennumtop} {
    @include media-breakpoint-down(sm) {
      margin-top: #{$gennumtop}px; } }
  .mrt-sm-#{$gennumtop} {
    @include media-breakpoint-up(sm) {
      margin-top: #{$gennumtop}px; } }
  .mrt-md-#{$gennumtop} {
    @include media-breakpoint-up(md) {
      margin-top: #{$gennumtop}px; } }
  .mrt-lg-#{$gennumtop} {
    @include media-breakpoint-up(lg) {
      margin-top: #{$gennumtop}px; } }
  .mrt-xl-#{$gennumtop} {
    @include media-breakpoint-up(xl) {
      margin-top: #{$gennumtop}px; } }
  .pdl-#{$gennumtop} {
    padding-left: #{$gennumtop}px; }
  .pdl-xs-#{$gennumtop} {
    @include media-breakpoint-down(sm) {
      padding-left: #{$gennumtop}px; } }
  .pdl-sm-#{$gennumtop} {
    @include media-breakpoint-up(sm) {
      padding-left: #{$gennumtop}px; } }
  .pdl-md-#{$gennumtop} {
    @include media-breakpoint-up(md) {
      padding-left: #{$gennumtop}px; } }
  .pdl-lg-#{$gennumtop} {
    @include media-breakpoint-up(lg) {
      padding-left: #{$gennumtop}px; } }
  .pdl-xl-#{$gennumtop} {
    @include media-breakpoint-up(xl) {
      padding-left: #{$gennumtop}px; } }
  $gennumtop: $gennumtop + 5; }
