      margin-bottom: #{$gennumbot}px; } }
  .pdr-#{$gennumbot} {
    padding-right: #{$gennumbot}px; }
  .pdr-xs-#{$gennumbot} {
    @include media-breakpoint-down(sm) {
      padding-right: #{$gennumbot}px; } }
  .pdr-sm-#{$gennumbot} {
    @include media-breakpoint-up(sm) {
      padding-right: #{$gennumbot}px; } }
  .pdr-md-#{$gennumbot} {
    @include media-breakpoint-up(md) {
      padding-right: #{$gennumbot}px; } }
  .pdr-lg-#{$gennumbot} {
    @include media-breakpoint-up(lg) {
